@keyframes showNotify{
    0%{
        opacity: 0.2;
        transform: translateY(-60px);
    }100%{
        opacity: 1;
        transform: translateY(0px);
    }
}

@keyframes hideNotify {
    0% {
        opacity: 1;
        transform: translateY(0px);
    } 100% {
        opacity: 0.2;
        transform: translateY(-60px);
    }
}


.wrapper-notify{
    position: fixed;
    top:16px;
    left:0 ;
    min-width: 100vw;
    z-index:10000000;
    transform: showNotify .18s linear 0s forwards;
    pointer-events: none;

}
.hide-notify{
    animation: hideNotify .18s linear 0s forwards;
}

.container-content{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 0 ;
    margin-top:-8px
}

.notify-container{
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 300px;
    padding: 10px 8px;
    border-radius:4px ;
    background: #545454eb;
}

.text-content{
    font-weight: 600;
    font-size: 1.4rem;
    line-height: 2.1rem;
    color: #fff;
}