.wrapper {
    display: flex;
    flex-direction: column;
    position: absolute;

    max-height: min((100vh - 96px) - 60px, 734px);
    max-height: 500px;
    min-height: 100px;
    padding-top: 8px;
    border-radius: 8px;
    background: rgb(255, 255, 255);
    box-shadow: rgb(0 0 0 / 12%) 0px 2px 12px;
    min-width: 100%;
}
