.menu-items {
    width: 244px;
    .menu-list {
        margin-left: 0;
    }
}
.menu-items button,
.menu-items a {
    display: block;
    margin: 0.5rem 0;
    text-align: left;
}

.menu-popper {
    padding-bottom: 8px;
}
.menu-body {
    overflow-y: auto;
}

//Menu wrapper
.menu-list {
    width: 100%;
    justify-content: flex-start;
    border-radius: 0;
    padding: 11px 16px;
    &.separate {
        border-top: 1px solid rgba(22, 24, 35, 0.12);
    }
    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }
}

//Header component
.header {
    flex-shrink: 0;
    position: relative;
    height: 50px;
    margin-top: -8px;
}

.back-btn {
    width: 50px;
    height: 100%;
    background-color: transparent;
    cursor: pointer;
    position: relative;
    left: 20px;
}

.header-title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
