.wrapper {
    max-width: 692px;
    margin: 0 auto;
}

.video-items {
    display: flex;
    align-items: flex-start;
    min-width: 80%;
    padding: 20px 0;
    max-width: 300px;
}

.video-items + .video-items {
    border-top: 1px solid rgba(22, 24, 35, 0.2);
}
.avatar {
    width: 56px;
    height: 56px;
    overflow: hidden;
    margin-right: 10px;
    flex-shrink: 0;
}

.avatar-user {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
}
.conatainer {
    flex: 1;
    margin-left: 12px;
}
.header-video {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.info {
    flex: 1;
    margin-right: 15px;
}

// .name,
// .container,
