//     display: flex;
// }

.name {
    align-items: baseline;
}
.nickname {
    margin-right: 5px;
    font-size: 1.8rem;
    color: #161823;
}
.username {
    font-size: 1.4rem;
    color: #161823;
}
.tags {
    span {
        font-weight: 600;
        color: #2b5db9;
        cursor: pointer;
    }
    &:hover {
        text-decoration: underline;
    }
}
.btn-follow .btn-unfollow {
    display: flex;
    align-items: center;
    min-width: 96px;
    height: 36px;
    font-weight: 600;
    border-radius: 4px;

    &:hover {
        color: #161823;
        border-color: #1618231f;
    }
}

.music {
    margin: 2px 0 12px 0;
}
.name-music {
    font-size: 1.4rem;
    color: #161823;
    cursor: pointer;
    &:hover {
        text-decoration: underline;
    }
}
.main-video {
    position: relative;
    display: flex;
    gap: 20px;
    height: auto;
}
