.control-wrapper {
    display: flex;
    align-items: center;
    position: relative;
    justify-content: center;
    width: 100%;
    height: 100%;
    cursor: pointer;
}
.volume-wrapper {
    &:hover .slider-container {
        opacity: 1;
        visibility: visible;
    }
}

.slider-container {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    bottom: 18px;
    left: 50%;
    transform: translateX(-50%);
    margin-bottom: 27px;
    transition-duration: 200ms;
}

.volume-slider {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 32px;
}
.btn-color {
    color: #34c759;
    text-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
}
.pause .play {
    color: aqua;
}
