.wrapper {
    padding: 9px 16px;
    // min-width: 48px;
    border-radius: 5px;
    font-size: 1.6rem;
    font-weight: 700;
    cursor: pointer;
    background-color: var(--white);
    border: 1px solid transparent;
    user-select: none;
    font-family: var(--font-family);
    + .wrapper {
        margin-left: 8px;
    }

    &.disable {
        cursor: not-allowed;
        opacity: 0.5;
    }
}

.icon + .title,
.title + .icon {
    margin-left: 8px;
}
.icon {
    display: inline-block;
    width: 24px;
    text-align: center;
}
//Btn types

.rounded {
    border-radius: 999px;
    box-shadow: 0 2px 8px rgba(0 0 0 /6%);
    border-color: rgba(22, 24, 35, 0.12);

    &:hover {
        background-color: rgba(22, 24, 35, 0.2);
        border-color: rgba(22, 24, 35, 0.03);
    }
}

.primary {
    color: var(--white);
    background-color: var(--primary);
    border-color: var(--primary);

    &:hover {
        border-color: var(--primary);
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)), #fe2c55;
    }
}

.outline {
    color: var(--primary);
    border-color: currentColor;

    &:hover {
        border-color: currentColor;
        background-color: rgba(254, 44, 85, 0.06);
    }
}

// Btn sizes
.small {
    padding: 4px 16px;
    font-size: 1.4rem;
    min-width: 88px;
}

.large {
    min-width: 140px;
    padding: 14px 16px;
}
.text {
    &:hover {
        text-decoration: underline;
    }
}
.container-btn {
    position: absolute;
    left: 125rem;
}
