.account-item {
    display: flex;
    align-items: center; /* ✅ vertically center avatar + text */
    padding: 8px;
    cursor: pointer;
}

.avatar {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
}

.label {
    font-size: 1.4rem;
    padding: 8px;
    font-weight: 600;
}

.wrapper {
    border-top: 1px solid #e3e3e4;
}
.item-info {
    flex: 1;
    margin-left: 12px;
    display: flex;
    flex-direction: column;
    justify-content: center; /* ✅ centers text inside the row */
}

.nickname {
    margin-top: 0;
    font-size: 1.5rem;
    color: #161823;
    line-height: 1;
}
.name {
    font-size: 1.1rem;
    opacity: #161823bf;
}
.check {
    font-size: 1.4rem;
    color: #20d5ec;
    margin-left: 4px;
}
.more-btn {
    font-size: 1.4rem;
    padding: 8px;
    color: #fe2c55;
    font-weight: 700;
    cursor: pointer;
}
